export class GameTracer {

    private _initSeed: string;
    private _randomNumbers: Array<number> = [];
    private _occuredEvents: Array<string> = [];
    private _executedActions: Array<[string, Array<number>]> = [];
    private _traces: Array<[string, [string, Array<number>]]> = [];

    constructor(randomSeed: string) {
        this._initSeed = randomSeed;
    }

    get initSeed(): string {
        return this._initSeed;
    }

    get randomNumbers(): Array<number> {
        return this._randomNumbers;
    }

    get occuredEvents(): Array<string> {
        return this.occuredEvents;
    }

    get executedActions(): Array<[string, Array<number>]> {
        return this._executedActions;
    }

    traceRandom(r: number): void {
        if (r === undefined || r === null)
            return
        this._randomNumbers.push(r);
    }

    traceEvent(e: string): void {
        if (e === undefined || e === null)
            return
        this._occuredEvents.push(e);
        this._traces.push(['event', [e, []]])
    }

    traceAction(a: [string, Array<number>]): void {
        if (a === undefined || a === null)
            return
        this._executedActions.push(a);
        this._traces.push(['action', a])
    }

    reset(newRandomSeed: string): void {
        this._initSeed = newRandomSeed;
        this._randomNumbers = [];
        this._occuredEvents = [];
        this._executedActions = [];
        this._traces = [];
    }

    async check(): Promise<void> {
        // submit all data to remote
        let json = JSON.stringify({
            "randomseed": this._initSeed,
            "randoms": this._randomNumbers,
            'traces': this._traces
        })

        fetch("/api/verify", {
            method: 'POST',
            body: json,
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
        }).then(data => {
            data.text().then(a => {
                console.log(a);
            });
        })
    }
}